<!--   Form -->
<template>
  <div>
    <div class="form-group">
      <header
        class="card-header"
        style="padding: 1.6rem 1rem;"
      >
        <h2 class="card-title">
          <span class="i-rounded bg-danger"><i class="icon-std-code" /></span>
        </h2>
        <div class="btn-container">
          <a
            href=""
            class="btn btn-primary"
            type="submit"
            @click.prevent="writeUserInfo"
          ><span class="hide"></span></a>
          <a
            href=""
            class="btn btn-primary"
            type="submit"
            @click.prevent="modifyCodeInfo"
          ><span class="hide"></span></a>
          <a
            href=""
            class="btn btn-primary"
            type="submit"
            @click.prevent="deleteCodeInfo"
          ><span class="hide"></span></a>
          <a
            href=""
            class="btn"
            @click="() => this.$router.push({ name: 'KAADM0301List' })"
          ><span class="hide"></span></a>
        </div>
      </header>
      <div class="ct-header">
        <button
          type="button"
          class="btn-filter collapsed d-xl-none"
          data-toggle="collapse"
          data-target="#collapse-filter"
        >
           <i class="icon-down" />
        </button>
        <div
          id="collapse-filter"
          class="collapse collapse-filter"
        >
          <div class="filter no-gutters">
            <div class="col">
              <label class="form-control-label">
                <b class="control-label">CODE</b>
                <input
                  v-model="code_name"
                  type="text"
                  class="form-control"
                  placeholder="Code ."
                >
              </label>
            </div>
            <div class="col">
              <label class="form-control-label">
                <b class="control-label">ID</b>
                <input
                  v-model="code_id"
                  type="text"
                  class="form-control"
                  placeholder="ID ."
                >
              </label>
            </div>
            <div class="col">
              <label class="form-control-label">
                <b class="control-label"></b>
                <input
                  v-model="code_content"
                  type="text"
                  class="form-control"
                  placeholder=" ."
                >
              </label>
            </div>
            <div class="col-auto">
              <button
                type="button"
                class="btn"
                @click.prevent="onSearch"
              >
                <i class="icon-srch" />
              </button>
            </div>
          </div>
        </div>
      </div>
      <div>
        <div class="col-lg-3">
          <div class="form-group">
            <v-treeview
              v-model="selection"
              :items="items"
              item-key="id"
              selectable
              selected-color="red"
              selection-type="leaf"
              return-object
              open-all
              open-on-click
              @input="selectCode"
            />
          </div>
        </div>
        <div style="float:left;">
          <div class="table-line" />
          <div class="table-responsive">
            <div class="ct-content">
              <div class="table-responsive">
                <table class="table">
                  <ag-grid-vue
                    style="width: 100%; height: 520px;"
                    class="flex-grow-1 flex-shrink-1 ag-theme-alpine"
                    :column-defs="columnDefs"
                    :column-types="columnTypes"
                    :row-data="rowData"
                    :grid-options="gridOptions"
                    :get-row-height="getRowHeight"
                    :modules="modules"
                    @cellEditorSelector="cellEditorSelector"
                    @onCellEditingStarted="onCellEditingStarted"
                    @onCellEditingStopped="onCellEditingStopped"
                    @getRowStyle="getRowStyle"
                    @gridReady="gridSizeFit"
                    @gridSizeChanged="gridSizeFit"
                    @cellValueChanged="cellValueChanged"
                    @paginationChanged="paginationChanged"
                  />
                </table>
              </div>
              <alert
                :dialog="isDialog"
                :send-data="alertContent"
                @close="close"
              />
              <failAlert
                :dialog="fDialog"
                :send-data="alertContent"
                @close="fDialog=false"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import 'ag-grid-community/dist/styles/ag-grid.css'
import 'ag-grid-community/dist/styles/ag-theme-alpine.css'
import { AllCommunityModules } from '@ag-grid-community/all-modules'
import { ClientSideRowModelModule } from '@ag-grid-community/client-side-row-model'
import { AgGridVue } from 'ag-grid-vue'

import alert from '@/components/common/CompletePOP.vue'
import failAlert from '@/components/common/FailPOP.vue'
import { codeList, srchCodeList, modifyCodeInfo } from '@/api/adm/Code.js'
// import { extractValuestest } from './util'

export default {
  name: 'KAKNM0103From',
  components: {
    AgGridVue,
    alert,
    failAlert
  },
  data: () => {
    return {
      // gridOptions
      columnDefs: null,
      columnTypes: null,
      getRowHeight: null,
      lists: [],
      rowData: [],
      modules: [ClientSideRowModelModule, AllCommunityModules],
      gridOptions: null,

      // form id
      userid: '', //  
      code_name: '',
      code_id: '',
      code_type: '',
      code_content: '',
      code_explain: '',
      code_up_id: '',
      code_order: '',
      code_display_yn: '',
      update_date: '',
      rows: [],
      selection: [],
      isDialog: false,
      fDialog: false,
      selectedCodes: '',
      alertContent: '',
      coList: [],
      codeList: [],
      items: [],
      item: []
    }
  },
  computed: {
    chg_userid () {
      return this.$store.state.userid
    }
  },
  beforeMount () {
    this.gridOptions = {
      // enableColResize: true,
      // enableSorting: true,
      // enableFilter: true,
      // animateRows: false,
      rowSelection: 'multiple',
      //   editable: true,
      autoHeight: true,
      treeData: true,
      pagination: true,
      paginationPageSize: 10,
      rowClassRules: null,
      excludeChildrenWhenTreeDataFiltering: true,
      suppressRowClickSelection: true,
      // have checkbox on the group column
      autoGroupColumnDef: {
        cellRendererParams: {
          checkbox: true
        }
      }
    }
    this.columnDefs = [
      { headerName: '', checkboxSelection: true },
      { headerName: '', field: 'code_name', sortable: true, filter: true },
      { headerName: 'ID', field: 'code_id', sortable: true, filter: true },
      { headerName: '', field: 'code_type', sortable: true, filter: true, editable: true },
      { headerName: '', field: 'code_content', sortable: true, filter: true, editable: true },
      { headerName: ' ', field: 'code_explain', sortable: true, filter: true, editable: true },
      { headerName: ' ID', field: 'code_up_id', sortable: true, filter: true },
      {
        headerName: '',
        field: 'code_order',
        sortable: true,
        type: 'numberColumn',
        filter: true,
        editable: true
      },
      {
        headerName: '',
        field: 'code_display_yn',
        sortable: true,
        filter: true,
        editable: true,
        cellEditor: 'select',
        cellEditorParams: { values: ['Y', 'N'] },
        refData: { Y: 'Y', N: 'N' }
      },
      { headerName: '', field: 'update_date', sortable: true, filter: true, type: ['dateColumn', 'nonEditableColumn'] }
    ]
    this.columnTypes = {
      numberColumn: {
        width: 130,
        filter: 'agNumberColumnFilter'
      },
      medalColumn: {
        width: 100,
        columnGroupShow: 'open',
        filter: false
      },
      nonEditableColumn: { editable: false },
      dateColumn: {
        filter: 'agDateColumnFilter',
        filterParams: {
          comparator: (filterLocalDateAtMidnight, cellValue) => {
            var dateParts = cellValue.split('/')
            var day = Number(dateParts[0])
            var month = Number(dateParts[1]) - 1
            var year = Number(dateParts[2])
            var cellDate = new Date(year, month, day)
            if (cellDate < filterLocalDateAtMidnight) {
              return -1
            } else if (cellDate > filterLocalDateAtMidnight) {
              return 1
            } else {
              return 0
            }
          }
        }
      }
    }
  },
  created () {
  },
  mounted () {
    console.log('mounted!!')

    // 
    codeList()
      .then((res) => {
        console.log('ddd', res.data)
        this.lists = res.data
        const list = this.lists
        // const codeList = this.convert_codeId(list)

        list.forEach(item => {
          for (let i = 0; i < list.length; i++) {
            if (item.code_up_id === list[i].code_id) {
              item.code_up_id = list[i].code_id
              break
            }
          }
        })
        console.log('foreach list', list)

        let myTree = null
        list.forEach(item => {
          if (myTree === null) {
            myTree = { id: item.code_id, name: item.code_type }
            console.log(';', myTree)
          } else {
            this.Recursive(item, myTree)
          }
        })

        const treeArr = []
        treeArr.push(myTree)
        this.items = treeArr
        this.myTree = myTree
      })
  },
  methods: {
    // 
    Recursive (item, tree) {
      // tree Id  item parentId 
      if (tree.id === item.code_up_id) {
        if (typeof tree.children === 'undefined') { // tree children 
          tree.children = []
          tree.children.push({ id: item.code_id, name: item.code_type })
        } else { // tree children .
          tree.children.push({ id: item.code_id, name: item.code_type })
        }
      } else { // tree Id item parentId 
        if (typeof tree.children === 'undefined') { // tree children 

        } else { // tree children 
          tree.children.forEach(child => {
            this.Recursive(item, child)
          })
        }
      }
    },
    selectCode () {
      // console.log('..', items)
      this.selectedCodes = [] // 
      for (let i = 0; i < this.selection.length; i++) {
        const codes = this.lists.find(item => {
          return item.code_id === this.selection[i].id
        })
        this.selectedCodes.push(codes)
      }

      this.makeData()

      // this.FATCH_USERLIST(selectedDept)
    },
    onSearch () {
      const srchData = {
        code_name: this.code_name,
        code_id: this.code_id,
        code_content: this.code_content
      }
      console.log('srchData', srchData)
      // 
      srchCodeList(srchData)
        .then((res) => {
          this.selectedCodes = res.data
          this.makeData()
          return res
        })
        .then((res) => console.log(res))
        .catch(console.error())
    },
    paginationChanged () {
      console.log('paginationChanged')
      console.log('search option ')
    },
    cellValueChanged () {
      console.log('gggg', this.rowData)
    },
    cellEditorSelector: function (params) {
      console.log(params)
      const data = params.data
      console.log('data=>', data)
      const fieldName = params.colDef.field
      if (fieldName === 'code_name') {
        console.log("fieldName === 'code_name'")
        return {
          params: {
            values: data.code_name
          }
        }
      }
      if (fieldName === 'code_id') {
        return {
          params: {
            values: data.code_id
          }
        }
      }
      if (fieldName === 'code_type') {
        console.log("fieldName === 'code_type'")
        return {
          params: {
            values: data.code_type
          }
        }
      }
      if (fieldName === 'code_content') {
        console.log("fieldName === 'code_content'")
        return {
          params: {
            values: data.code_content
          }
        }
      }
      if (fieldName === 'code_explain') {
        console.log("fieldName === 'code_explain'")
        return {
          params: {
            values: data.code_explain
          }
        }
      }
      if (fieldName === 'code_order') {
        console.log("fieldName === 'code_order'")
        return {
          params: {
            values: data.code_order
          }
        }
      }
    },
    onRowEditingStarted: function (event) {
      console.log('never called - not doing row editing')
    },
    onRowEditingStopped: function (event) {
      console.log('never called - not doing row editing')
    },
    onCellEditingStarted: function (event) {
      console.log('cellEditingStarted')
    },
    onCellEditingStopped: function (event) {
      console.log('cellEditingStopped')
    },
    // rowData  
    makeData () {
      this.rowData = []
      this.selectedCodes.forEach(e => {
        const value = {
          code_name: e.code_name,
          code_id: e.code_id, // 
          code_type: e.code_type,
          code_content: e.code_content,
          code_explain: e.code_explain,
          code_up_id: e.code_up_id,
          code_order: e.code_order,
          code_display_yn: e.code_display_yn,
          update_date: e.update_date
        }
        this.rowData.push(value)
      })
    },
    gridSizeFit (params) {
      console.log('gridSizeFit')
      //         
      if (window.innerWidth > 800) { //   800 px   
        console.log('innerWidth')
        params.api.sizeColumnsToFit() //          
      } else {
      //     
        const allColumnIds = []
        this.gridOptions.columnApi.getAllColumns().forEach(function (column) {
          allColumnIds.push(column.colId)
        })
        this.gridOptions.columnApi.autoSizeColumns(allColumnIds)
      }
    },
    setGroupHeight (height) {
      this.groupHeight = height
      this.gridApi.resetRowHeights()
    },
    getRowStyle: function (param) {
      return { 'text-align': 'center' }
    },
    // 
    modifyCodeInfo () {
      console.log('modifyCodeInfo...')
      const formData = []
      this.rows = this.gridOptions.api.getSelectedRows()

      console.log('rows', this.rows)
      if (this.rows.length === 0) {
        this.alertContent = ' .'
        this.fDialog = true
      }

      for (let i = 0; i < this.rows.length; i++) {
        const e = this.rows[i]
        const value = {
          userid: this.$store.state.userid,
          code_name: e.code_name,
          code_id: e.code_id,
          code_up_id: e.code_up_id,
          code_type: e.code_type,
          code_order: e.code_order,
          code_content: e.code_content,
          code_explain: e.code_explain,
          code_display_yn: e.code_display_yn,
          flag: 'M'
        }
        formData.push(value)
      }
      console.log('formData', formData)

      // 
      modifyCodeInfo(formData)
        .then((res) => {
          console.log('res=>>', res)
          this.alertContent = this.rows.length + ' .'
          this.isDialog = true
        })
        .catch(console.error())
    },
    // 
    deleteCodeInfo () {
      console.log('modifyCodeInfo...')
      if (this.rows.length === 0) {
        this.alertContent = ' .'
        this.fDialog = true
      }
      const formData = []
      this.rows = this.gridOptions.api.getSelectedRows()
      console.log('rows', this.rows)

      for (let i = 0; i < this.rows.length; i++) {
        const e = this.rows[i]
        const value = {
          code_id: e.code_id,
          flag: 'D'
        }
        formData.push(value)
      }
      modifyCodeInfo(formData)
        .then((res) => {
          console.log('res=>>', res)
          this.alertContent = this.rows.length + ' .'
          this.isDialog = true
        })
        .catch(console.error())
    },
    // 
    writeUserInfo () {
      this.$router.push({ name: 'KAADM0302Wirte' })
    },
    //  
    close () {
      this.$router.go(this.$router.currentRoute)
    }
  }
}
</script>

<style lang="scss" scoped>
.col-lg-3 {
  width: 300px;
  float: left;
}
.table-responsive {
  width: 1320px;
